
import React, { useContext, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { FirebaseContext } from '../context/firebase';
import { Form } from "../components";
import { HeaderContainer } from '../containers/header';
import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';


export default function  SignUp() {
   const { firebase } = useContext(FirebaseContext);
  const history = useHistory();
    
    //     
   

    const [firstName, setFirstName] = useState('');
    const [emailAddress, setEmailAddress] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
  
    const isInvalid = firstName === '' || password === '' || emailAddress === '';
  
    //    &&     
   //  const isInvalid = emailAdress === '' || password === '';
 

    const handleSignup = (event) => {
       event.preventDefault();

       //   auth  Email Password  
       //  history  Router  Browse  
       return firebase
         .auth()
         .createUserWithEmailAndPassword(emailAddress,password)
         .then((result) =>
            result.user
           .updateProfile({
             displayName: firstName,
             photoURL: Math.floor(Math.random() * 5) + 1,
           })
           .then(() => {
             history.push(ROUTES.BROWSE);
           })
       )
      .catch((error) => { 
             //        
             setFirstName('');
             setEmailAddress('');
             setPassword('');
             setError(error.message)
         })        
    }

    //   
    return (
       <>
         <HeaderContainer>
            <Form>
              <Form.Title> </Form.Title>
              {/*    */}
              {error && <Form.Error data-testid="error">    .
              </Form.Error>}
       
              <Form.Base onSubmit={handleSignup} method="POST">
                  <Form.Input
                  placeholder=" "
                  value={firstName}
                  onChange={({ target }) => setFirstName(target.value)}
                  />
                     
                 <Form.Input 
                    placeholder="    "  
                    value={emailAddress}
                    onChange={({ target }) => setEmailAddress(target.value)} 
                />
                <Form.Input 
                   type="password"
                   value={password}
                   autoComplate='off'
                   placeholder=""
                   onChange={({ target }) => setPassword(target.value)}
                />
                <Form.Submit type="submit" disabled={isInvalid} data-testid="sign-up">
                   
                </Form.Submit>
              </Form.Base>
              <Form.Text>
                Netflix  ? <Form.Link to={ROUTES.SIGN_UP}> </Form.Link>
              </Form.Text>
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </HeaderContainer>
            <FooterContainer/>
       </>  
    )
}

 